import {
	PanelCtrl
} from 'app/plugins/sdk';
import moment from 'moment';
import _ from 'lodash';
import $ from 'jquery';

var panelDefaults = {
	colors: "rgba(8, 94, 157, 0.9)",
	size: '100%',
	NA: '',
	CK:'uncheck',
	url: '', //默认显示模式
};
export class ButtonPanelCtrl extends PanelCtrl {
	constructor($scope, $injector, templateSrv, $sce) {
		super($scope, $injector);
		this.templateSrv = templateSrv;
		this.$sce = $sce;

		_.defaults(this.panel, panelDefaults); //将panelDefaults属性附加到this.panel上
		var CheckOption = [{
			name: '选中',
			value: 'check'
		}, {
			name: '未选中',
			value: 'uncheck'
		}];
		this.panel.CheckOption = CheckOption;
		var fontSizes = ['80%', '90%', '100%', '110%', '120%', '130%', '150%', '160%', '180%', '200%', '220%', '250%'];
		this.panel.fontSizes = fontSizes;
		this.events.on('init-edit-mode', this.onInitEditMode.bind(this));
		this.events.on('data-received', this.onDataReceived.bind(this));
		this.events.on('render', this.onRender.bind(this));
	}

	onInitEditMode() {
		this.addEditorTab('Options', 'public/plugins/oge_button/editor.html', 2);
	}

	onDataReceived(dataList) {
		this.data = dataList;
		this.render();
	}
onRender() {
	}
	link(scope, elem, attrs, ctrl) {
		ctrl.events.on('render', function() {	
		
		});
	}
}
ButtonPanelCtrl.templateUrl = 'module.html';